.containerPaper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title{
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 26px;
    line-height: 32px;
    color: #000;
    margin-top: 35px;
}

.img {
    width: 108px;
    height: 108px;
    margin-top: 29px;
}

.text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    color: #000;
    opacity: 0.5;
    margin-top: 31px;
}
.button {
    width: 347px;
    border-radius: 30px;
    margin-top: 41px;
    background: #366EFF;
    letter-spacing: 0.01em;
}